.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  margin: 0px;
  width: 100%;
  list-style-type: none;
  text-align: center;
  bottom: -25px;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  cursor: pointer;
  padding: 0px;
  width: 20px;
  height: 20px;
  /* margin: 0 5px; */
  margin: 0 10px;
}

.slick-dots li button {
  display: block;
  cursor: pointer;
  border-width: 0px;
  color: transparent;
  font-size: 0;
  line-height: 0;
  width: 20px;
  height: 20px;
  padding: 5px;
  outline: 0;
  background: 0 0;
}

.slick-dots li button:focus,
.slick-dots li button:hover {
  outline: 0;
}

.slick-dots li button:focus:before, .slick-dots li button:hover:before {
  opacity: 1;
}

.slick-dots li button:before {
  position: absolute;
  top: 0px;
  left: 0px;
  text-align: center;
  --tw-text-opacity: 1;
  color: rgba(0, 0, 0, var(--tw-text-opacity));
  opacity: 0.25;
  font-size: 6px;
  line-height: 20px;
  width: 20px;
  height: 20px;
  content: "•";
}

.slick-dots li.slick-active button:before {
  --tw-text-opacity: 1;
  color: rgba(0, 0, 0, var(--tw-text-opacity));
  opacity: 0.75;
}

.blue-dots li.slick-active div span {
  opacity: 1;
}

.blue-dots li div span {
  opacity: 0;
}

/* Hide scrollbar for Chrome, Safari and Opera */

.hide-scrollbar::-webkit-scrollbar {
